import { Component, OnInit, ViewChild } from '@angular/core';
import { firstValueFrom } from 'rxjs';
import { MatTableDataSource } from '@angular/material/table'
import { MatButton } from '@angular/material/button';
import { SaveComponent } from './save/save.component';
import { JedsMenuService } from '@shared/service/app/menu.service';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { ProveedoresService } from '../service/proveedores.service';
import { IProveedor } from '../models/proveedor';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
@Component({
    selector: 'app-proveedores',
    templateUrl: './proveedores.component.html',
    styleUrls: ['./proveedores.component.scss']
})
export class ProveedoresComponent implements OnInit {
    proveedores: IProveedor[];
    public dataSource: MatTableDataSource<IProveedor> = new MatTableDataSource();
    columns = ['cuit', 'razon_social', 'direccion', 'localidad', 'telefono', 'email', 'contacto', 'numero_contacto', 'acciones']
    @ViewChild(MatPaginator) paginator!: MatPaginator;
    // MatPaginator atributos
    totalRows = 0;
    pageSize = 25;
    currentPage = 0;
    pageSizeOptions: number[] = [5, 10, 25, 50, 100];
    // MatPaginator Output
    pageEvent: PageEvent;
    sortValue: string;

    constructor(
        private proveedoresService: ProveedoresService,
        private menu: JedsMenuService,
        private confirm: FuseConfirmationService,
        private alert: AppAlertService
    ) {
    }
    ngOnInit(): void {
        this.getProveedores();
    }

    /**
     * Esta funcion escucha el cambio de orden de la tabla
     */
    _sortChange($event?) {
        if ($event) {
            if ($event.direction == 'desc') {
                this.sortValue = $event.direction;
            } else if ($event.direction == 'asc') {
                this.sortValue == $event.direction;
            } else {
                this.sortValue = 'asc'
            }
        }
        this.getProveedores(this.sortValue)
    }

    /**
     *  -Funcion en el componente.-
     * - Funcion que comunica el componente con los servicios.
     * - Espera que se consulten los proveedores del backend para almacenarlos en una variable.
     * - Teniendo la respuesta del backend en una variable, llama a la funcion 'getDataproveedores' pasandole por parametro los proveedores.
     */
    async getProveedores(sort?: any) {
        //Le envia el filtro al servicio y recibe como respuesta los proveedores paginados con los valores que seteamos antes del constructor.
        const proveedores = await firstValueFrom(this.proveedoresService.getProveedores({ pagination: 1, perpage: this.pageSize, currentPage: this.currentPage + 1, sort }));
        this.getDataProveedores(proveedores);
    }
    /**
     *  -Funcion en el componente-
     * - Esta funcion toma los datos que se la pasan por parametro para crear una tabla con AngularMaterial.
     * @param data Aca se almacenan los proveedores.
     */
    async getDataProveedores(data) {
        new Promise(async (resolve) => {
            this.proveedores = data.list;
            if (!!this.paginator) {
                this.paginator.pageIndex = this.currentPage;
                this.paginator.length = data['totalCount'];
            }
            this.dataSource = new MatTableDataSource(this.proveedores);
            resolve(true);
        });
    }

    /**
     * -Funcion en el componente-
     * * Setea la nueva cantidad de items que se quiere mostrar por pagina a las variables del componente y asi actualizar la pagina con la nueva cantidad.
     * @param event Es la modificacion en el template de cuantos items se quiere mostrar por pagina.
     */
    actualizarPagina(event: PageEvent) {
        this.pageSize = event.pageSize;
        this.currentPage = event.pageIndex;
        this.getProveedores();
    }

    /**
     * Funcion en el componente
     * - Esta funcion guarda un Proveedor.
     * @param documentRef 
     */
    saveProveedor(documentRef: MatButton) {
        this.menu
            .open(SaveComponent, documentRef._elementRef, {
                panelClass: "my-panel"
            })
            .afterClosed()
            .subscribe({
                next: response => {
                    if (response) {
                        this._sortChange();
                    }
                },
            });
    }
    /**
     * Funcion en el componente
     * - Esta funcion actualiza un Proveedor.
     * @param documentRef 
     * @param Proveedor 
     */
    updateProveedor(documentRef: MatButton, Proveedor: IProveedor) {
        this.menu
            .open(SaveComponent, documentRef._elementRef, Proveedor)
            .afterClosed()
            .subscribe({
                next: response => {
                    if (response) {
                        this._sortChange()
                    }
                },
            });
    }
    /**
     * Funcion en el componente
     * - Esta funcion elimina un Proveedor.
     * @param documentRef 
     * @param Proveedor 
     */
    deleteProveedor(Proveedor: IProveedor) {
        this.confirm.open({
            title: `Eliminar Proveedor`,
            message: `¿Desea eliminar el Proveedor: "${Proveedor.RazonSocial}"?`,
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this.proveedoresService.deleteProveedor(Proveedor.Codigo).subscribe({
                        next: (data) => {
                            // Manejar la respuesta exitosa
                            this.alert.success("Proveedor eliminado con éxito.");
                            if (res) {
                                this._sortChange()
                            }
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error('Error en el servicio:', error);
                            this.alert.error("Este articulo no puede eliminarse. Está asociado a pedidos de materiales.");
                        }
                    });
                }
            });
    }
}